import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastrModule } from 'ngx-toastr';
import { AppComponent } from './app.component';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { ErrorInterceptor, JwtInterceptor } from './service/_helpers';
import { WMS_CONFIG } from './config';
import { CUSTOM_CONFIG } from './service/config.service';
import { AppRoutingModule } from './app-routing.module';
import { HeaderComponent } from './layout/header/header.component';

import { NotificationsModule } from './common-module/notifications/notifications.module';
import { ProfileDropModule } from './common-module/profile-drop/profile-drop.module';
import { DashboardBoxModule } from './common-module/dashboard-box/dashboard-box.module';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    AppRoutingModule,
    HttpClientModule,
    BrowserAnimationsModule, // required animations module
    ToastrModule.forRoot(), // ToastrModule added

    NotificationsModule,
    ProfileDropModule,
    DashboardBoxModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    { provide: CUSTOM_CONFIG, useValue: WMS_CONFIG }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
