import { Injectable } from '@angular/core';
import { ReplaySubject, BehaviorSubject } from 'rxjs';
import 'rxjs/add/operator/distinctUntilChanged';
import { JwtService } from './jwt.service';
import { encryptValue } from '../common';
import { CommonService } from './common.service';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  // for storing current user details
  public currentUserSubject = new BehaviorSubject(null);
  public currentUser = this.currentUserSubject.asObservable().distinctUntilChanged();

  // for checking user is authneticated or not
  public isAuthenticatedSubject = new BehaviorSubject<boolean>(false);
  public isAuthenticated = this.isAuthenticatedSubject.asObservable();

  // for storing current company details
  private currentCompanySubject = new BehaviorSubject(null);
  public currentCompany = this.currentCompanySubject.asObservable().distinctUntilChanged();

  // for checking company is selected or not
  private isCompanySelectedSubject = new BehaviorSubject<boolean>(false);
  public isCompanySelected = this.isCompanySelectedSubject.asObservable();

  // for storing common lists details
  private commonListSubject = new BehaviorSubject(null);
  public commonList = this.commonListSubject.asObservable().distinctUntilChanged();

  // for storing current user permission details
  public userPermissionSubject = new ReplaySubject(1);
  public permission = this.userPermissionSubject.asObservable().distinctUntilChanged();


  // Order view Page Detils

  public OrderDetailsSubject = new BehaviorSubject(null);
  public OrderDetails = this.OrderDetailsSubject.asObservable();

  // Order view Page Detils

  public SalesOrderDetailsSubject = new BehaviorSubject(null);
  public SalesOrderDetails = this.SalesOrderDetailsSubject.asObservable();


  constructor(
    private jwtService: JwtService,
    private commonService: CommonService
  ) { }

  saveToken(token) {
    this.jwtService.saveToken(encryptValue(token));
  }

  saveCommonList(data) {
    this.commonListSubject.next(data);
  }

  setAuth(user) {
    this.saveToken(user.Token['token']);
    this.updateAuth(user.User);
    this.updatePermission(user.Permission)
    if (user.Company && user.Company.company_id) {
      this.updateCompany(user.Company);
    }
  }

  updateAuth(data) {
    this.currentUserSubject.next(data);
    this.isAuthenticatedSubject.next(true);
  }

  updatePermission(data) {
    this.userPermissionSubject.next(data);
  }

  purgeAuth() {
    this.jwtService.destroyToken();
    // Set auth status to false
    this.isAuthenticatedSubject.next(false);
    this.currentUserSubject.next(null);
    this.userPermissionSubject.next([]);
    this.purgeCompany();
  }

  updateCompany(data) {
    this.currentCompanySubject.next(data);
    this.jwtService.saveCompanyId(data.company_id);
    this.jwtService.saveCompanyName(data.company_name);
    this.isCompanySelectedSubject.next(true);
  }

  purgeCompany() {
    this.jwtService.destroyCompanyId();
    this.jwtService.destroyCompanyName();
    this.currentCompanySubject.next(null);
    this.isCompanySelectedSubject.next(false);
  }
}
