import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginRedirect, EnsureAuthenticated, IsCompanySelected, CompanyRedirectService } from './service/_guards';


const routes: Routes = [

  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full'
  },
  {
    path: 'login',
    loadChildren: () => import('./auth/login/login.module').then(m => m.LoginModule),
    canActivate: [LoginRedirect],
    data: { title: 'login' },
  },
  // {
  //   path: 'signup',
  //   loadChildren: () => import('./auth/signup/signup.module').then(m => m.SignupModule),
  //   canActivate: [LoginRedirect],
  //   data: { title: 'singup' },
  // },
  {
    path: 'forgot-password',
    loadChildren: () => import('./auth/forgot-password/forgot-password.module').then(m => m.ForgotPasswordModule),
    canActivate: [LoginRedirect],
    data: { title: 'forgot_password' },
  },
  {
    path: 'reset-password/:token',
    loadChildren: () => import('./auth/reset-password/reset-password.module').then(m => m.ResetPasswordModule),
    canActivate: [LoginRedirect],
    data: { title: 'reset_password' },
  },
  {
    path: 'link-expired',
    loadChildren: () => import('./auth/message/message.module').then(m => m.MessageModule),
    canActivate: [LoginRedirect],
    data: { title: 'link_expired' },
  },
  {
    path: 'dashboard',
    loadChildren: () => import('./pages/dashboard/dashboard.module').then(m => m.DashboardModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'company',
    loadChildren: () => import('./pages/company/company-list/company-list.module').then(m => m.CompanyListModule),
    canActivate: [EnsureAuthenticated, CompanyRedirectService],
    data: { title: 'company_list', module: 'company', action: 'list' },
  },
  {
    path: 'manage',
    loadChildren: () => import('./pages/master/master.module').then(m => m.MasterModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
  },
  {
    path: 'inbound',
    loadChildren: () => import('./pages/inbound/inbound.module').then(m => m.InboundModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
  },
  {
    path: 'profile',
    loadChildren: () => import('./pages/profile/profile.module').then(m => m.ProfileModule),
    canActivate: [EnsureAuthenticated],
  },
  {
    path: 'setting',
    loadChildren: () => import('./pages/company-setting/company-setting-view/company-setting-view.module').then(m => m.CompanySettingViewModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
  },
  {
    path: 'stock',
    loadChildren: () => import('./pages/operation/operation.module').then(m => m.OperationModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
  },
  {
    path: 'outbound',
    loadChildren: () => import('./pages/outbound/outbound.module').then(m => m.OutboundModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
